.App {
  text-align: center;
  width:100%;
  color:#fff;
}
#first{
  
  padding:2%;
}
.particles{
  position: fixed;
  width: 100%;
  
}
.Name{
  font-family: 'Architects Daughter', cursive;
  color:aquamarine;
  text-align: left;
  clear:left;
  margin-left:1%;
  padding: 0.4%;
  padding-left: 0.6%;
  font-style: oblique;
  font-weight: bold;
  float:left;
  font-size: 3vw;
  letter-spacing: 30%;
}

.Mainnav{
  float: right;
    background-color: cadetblue;
    opacity: 80%;
    margin: 1%;
    margin-right: 1.7%;
    margin-top: 2%;
    padding: 1%;
    /* width: 8rem; */
    height: auto;
    color: #fff;
    font-family: "Kaushan Script",Monospace;
    text-align: center;
    text-decoration: none;
    border-radius: 3%;
    -moz-border-radius: 3%;
    -o-border-radius: 3%;
    -webkit-border-radius: 3%;
}
.break{
  color: #fff;
  font-family :"Kaushan Script",Monospace;
  text-align: center;
  text-decoration: none;
  padding:0%;
  margin:1rem;
  font-size:1.5vw ;
}
.Mainnav:visited{

  color: #fff;
}
.Mainnav:hover{
  
  opacity:100%;
  cursor: pointer;
  transform:scale(1.2,1.0);
}
.dp{
  clear:left;
  margin: auto;
  padding: 3%;
  border-radius: 30%;
  width: 25%;
  height: 35%;
  display: block;
  align-items: center;
  position: relative;
  z-index: 1;


}
.intro
{
  font-family: 'Architects Daughter', cursive;
    color: aquamarine;
    display: inline-block;
    margin-top: 4%;
    margin-left: 3%;
    margin-right: 5%;
    font-size: 2vw;
    text-align: center;
    font-weight: bolder;
    font-style: italic;
    letter-spacing: 20%;
}
.grad{
  font-size:10vw;
}
.heading{
  text-align: left;
    font-family:"Rock Salt",Monospace;
    padding : 2%;
    color: aquamarine;
    font-size:1.1vw;
}
.about{
  font-size: 1.5vw;
}
#second{
  
  padding:2%;

}
#third{
  
  padding:2%;
}
#fourth{
  
  padding:2%;
}
.clg{
  font-size: 1.75vw;
  color: aquamarine;
  margin:4.5%;
}
.c{
    margin-left: 3%;
    margin-right: 6%;
    display: inline-block;
    font-size: 3vw;
}
.c p{
  text-align: left;
  display: inline-block;
  margin:1rem;
  padding:1rem;
  font-size: 1.8vw;
}

.e{
  margin-left: 6%;
    margin-right: 3%;
    display: inline-block;
    font-size: 3vw;
}
.e p{
  text-align: left;
  display: inline-block;
  margin:1rem;
  padding:1.5rem;
  font-size: 1.8vw;
}

.fa {
  padding: 2%;
  font-size: 1vw;
    width: 3.5%;
    margin: 3% 3%;
    text-align: center;
    text-decoration: none;
    border-radius: 50%;
    position: relative;
}
#fifth{
  padding:2% 0%;
  padding-bottom: 0%;
}

.fa:hover {
  opacity: 0.8;
  cursor:pointer;
}

.fa-facebook {
  background: #3B5998;
  color: white;
}


.fa-twitter {
  background: #55ACEE;
  color: white;
}
.fa-instagram {
  background: #e95950;
  color: white;
}
.fa-linkedin {
  background: #0077b5;
  color: white;
}
.fa-github{
  
  padding:1%;
  color:black;
  margin:0% 2%;
}

.footer{
  background-color: cadetblue;
  margin-top:2.5%;
  margin-bottom: 0%;
  padding:1.2%;
  font-size: 1.2vw;
  
}
.tool{
  width: 5rem;
  font-size: 1vw;
}
.skillset{
  display: inline-block;
  margin:3% 5%;
  background-color: cadetblue;
  padding: 1% 1.5%;

  
}
.projects{
  display: inline-block;
   border-radius: 15%;
  -moz-border-radius: 15%;
  -o-border-radius: 15%;
  -webkit-border-radius: 15%;
  border : solid cadetblue;
  color: #fff;
  padding: 2% 2%;
  margin:2%;
  width:60%;
 
}
.projname{
  color:aquamarine;
  margin:1%;
  margin-top:2%;
  font-size: 2vw;
}
.projdesc{
  margin:6% 6%;
  font-size: 1.25vw;
  text-align: center;
  height:8vw;
  
}
span {
  display: inline-block;
  vertical-align: middle;
  line-height: normal;
  padding:5%;
}
.projlink{
  position: relative;
  margin:5% 2%;
  color:cadetblue;
  text-decoration: none;
  font-size: 1vw;
}
.projlink:visited{
  color:cadetblue;
  text-decoration: none;
}


@media (min-width: 850px)
{

.fa {
  padding: 2%;
  font-size: 1.7vw;
  width: 2%;
  }
.projects{
  width:40%;
  
}
.projdesc{
  height:15vh;
  
}
}

@media (min-width: 1200px){
  .projects{
    width:30%;
    height: 45vh;
    
  }
  .projdesc{
    height:30vh;
    
  }

}
@media (min-width: 1500px){
  .projects{
    width:25%;
    height: 53vh;
    
  }
  .projdesc{
    height:38vh;
    
  }

}